/* =========== Home ============ */

.body {
  max-width: 1650px;
  margin: 100px auto;
}

.loading {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  color: black;
}

.grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 100px;
  color: black;
}

.moviebox {
  display: flex;
  padding: 60px;
  background-color: white;
  box-shadow: 2px 2px 5px black;
  border-radius: 10px;
}
.moviebox img {
  margin-right: 20px;
}

li {
  all: unset;
  margin-right: 15px;
}

